import React, { useState, useEffect, useRef } from 'react';
import { View, Text, TextInput, Button, FlatList, StyleSheet, TouchableOpacity, KeyboardAvoidingView, Platform } from 'react-native';
import { auth, db } from './firebaseConfig';
import Swal from 'sweetalert2';
import Modal from 'react-modal';

import { 
  signInWithEmailAndPassword, 
  createUserWithEmailAndPassword, 
  onAuthStateChanged, 
  signOut 
} from 'firebase/auth';
import { 
  collection, 
  doc, 
  setDoc, 
  getDoc, 
  onSnapshot, 
  query, 
  orderBy, 
  getDocs, 
  addDoc 
} from 'firebase/firestore';

const App = () => {
  const [user, setUser] = useState(null);
  const [users, setUsers] = useState([]);
  const [selectedChatUser, setSelectedChatUser] = useState(null);
  const [currentPage, setCurrentPage] = useState('home');
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState(null);
  const messageInputRef = useRef(null);

  useEffect(() => {
    if (selectedChatUser) {
        createChatRoom(selectedChatUser);
    }
  }, [selectedChatUser]);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        loadUsers();
      }
    });

    return () => unsubscribe();
  }, []);

  const loadUsers = async () => {
    const usersSnapshot = await getDocs(collection(db, 'users'));
    const usersList = usersSnapshot.docs.map(doc => ({
      uid: doc.id,
      ...doc.data()
    })).filter(u => u.uid !== user?.uid);
    setUsers(usersList);
  };

  const handleLogin = async () => {
    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const userDoc = await getDoc(doc(db, 'users', userCredential.user.uid));

      if (userDoc.exists() && !userDoc.data().approved) {
        await signOut(auth);
        Swal.fire({
          title: 'Onay Bekleniyor',
          text: 'Hesabnz henz onaylanmad. Ltfen ynetici onayn bekleyin.',
          icon: 'warning',
        });
      } else {
        Swal.fire({
          title: 'Giri Baarl!',
          text: 'Baaryla giri yaptnz.',
          icon: 'success',
        });
      }
    } catch (error) {
      let errorMessage = '';

      switch (error.code) {
        case 'auth/user-not-found':
          errorMessage = 'Bu e-posta adresi ile kaytl bir kullanc bulunamad.';
          break;
        case 'auth/wrong-password':
          errorMessage = 'Girdiiniz ifre yanl. Ltfen tekrar deneyin.';
          break;
        case 'auth/invalid-email':
          errorMessage = 'Geersiz e-posta adresi girdiniz. Ltfen kontrol edin.';
          break;
        case 'auth/user-disabled':
          errorMessage = 'Bu kullanc hesab devre d braklm.';
          break;
        default:
          errorMessage = 'Giri srasnda bir hata olutu. Ltfen tekrar deneyin.';
          break;
      }

      Swal.fire({
        title: 'Giri Hatas',
        text: errorMessage,
        icon: 'error',
      });
    }
  };

  const handleSignUp = async () => {
    try {
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const newUser = userCredential.user;
      await setDoc(doc(db, 'users', newUser.uid), {
        email: newUser.email,
        approved: false
      });

      await signOut(auth);

      Swal.fire({
        title: 'Kayt Baarl',
        text: 'Kayt ileminiz baarl. Hesabnz onaylandktan sonra giri yapabileceksiniz.',
        icon: 'success',
      });
    } catch (error) {
      let errorMessage = '';

      switch (error.code) {
        case 'auth/email-already-in-use':
          errorMessage = 'Bu e-posta adresi zaten kullanmda. Farkl bir e-posta deneyin.';
          break;
        case 'auth/weak-password':
          errorMessage = 'ifreniz ok zayf. Ltfen daha gl bir ifre sein.';
          break;
        case 'auth/invalid-email':
          errorMessage = 'Geersiz e-posta adresi girdiniz. Ltfen kontrol edin.';
          break;
        default:
          errorMessage = 'Kayt srasnda bir hata olutu. Ltfen tekrar deneyin.';
          break;
      }

      Swal.fire({
        title: 'Kayt Hatas',
        text: errorMessage,
        icon: 'error',
      });
    }
  };

  const handleOptionClick = (option) => {
    if (option === 'testPage') {
      setSelectedChatUser(selectedUser);
      setCurrentPage('testPage');
    } else if (option === 'sendMessage') {
      setSelectedChatUser(selectedUser);
      setCurrentPage('chat');
    }
    setModalIsOpen(false); // Modal kapatyoruz
  };

  const handleMenuClick = (user) => {
    setSelectedUser(user);
    setModalIsOpen(true); // Modal ayoruz
  };

  const createChatRoom = async (selectedUser) => {
    const chatRoomId = user?.uid > selectedUser.uid
        ? `${user.uid}_${selectedUser.uid}`
        : `${selectedUser.uid}_${user?.uid}`;

    setSelectedChatUser(selectedUser);

    const q = query(
        collection(db, 'chatRooms', chatRoomId, 'messages'),
        orderBy('createdAt', 'asc')
    );

    const unsubscribe = onSnapshot(q, (querySnapshot) => {
        const messagesList = querySnapshot.docs.map(doc => ({
            id: doc.id,
            ...doc.data()
        }));

        console.log("Yklenen Mesajlar:", messagesList);
        setMessages(messagesList);
    });

    return () => unsubscribe();
};

const handleSendMessageAndKeepFocus = async () => {
  await handleSendMessage();
  if (messageInputRef.current) {
    messageInputRef.current.focus(); // Mesaj gnderildikten sonra tekrar odaklanr
  }
};

const handleSendMessage = async () => {
  if (message.length > 0 && selectedChatUser) {
    const chatRoomId = user?.uid > selectedChatUser.uid
      ? `${user.uid}_${selectedChatUser.uid}`
      : `${selectedChatUser.uid}_${user?.uid}`;

    await addDoc(collection(db, 'chatRooms', chatRoomId, 'messages'), {
      text: message,
      createdAt: new Date(),
      user: user?.email
    });
    setMessage('');
  }
};

  if (!user) {
    return (
      <View style={styles.loginContainer}>
        <Text style={styles.heading}>Giri Yap</Text>
        <TextInput
          value={email}
          onChangeText={setEmail}
          style={styles.input}
          placeholder="Email"
        />
        <TextInput
          value={password}
          onChangeText={setPassword}
          secureTextEntry
          style={styles.input}
          placeholder="ifre"
        />
        <View style={{ flexDirection: 'row', justifyContent: 'space-between', marginTop: 10 }}>
  <Button title="Giri Yap" onPress={handleLogin} color="#FF7F50" style={styles.button} />
  <Button title="Kayt ol" onPress={handleSignUp} color="#FF7F50" style={styles.button} />
</View>

      </View>
    );
  }

  if (currentPage === 'home') {
    return (
      <View style={styles.container}>
        <Text style={styles.title}>Kullanclar</Text>
        <View style={styles.userList}>
          {users.map((item) => (
            <TouchableOpacity
              key={item.uid}
              style={styles.userRectangle}
              onPress={() => handleMenuClick(item)}
            >
              <Text style={styles.email}>{item.email}</Text>
              <Text style={styles.menuIcon}>&#x2022;&#x2022;&#x2022;</Text>
            </TouchableOpacity>
          ))}
        </View>
        <Button title="k Yap" onPress={() => signOut(auth)} color="#FF7F50" style={styles.button} />

        <Modal
  isOpen={modalIsOpen}
  onRequestClose={() => setModalIsOpen(false)}
  contentLabel="Men"
  style={{
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      padding: '20px',
      borderRadius: '12px',
      backgroundColor: '#fff',
      boxShadow: '0 4px 10px rgba(0, 0, 0, 0.1)',
    },
  }}
>
  <Text style={{ fontSize: 20, color: '#FF7F50', marginBottom: 15 }}>Seenekler</Text>
  <TouchableOpacity 
    style={{ 
      backgroundColor: '#FF7F50', 
      padding: 10, 
      borderRadius: 8, 
      marginBottom: 10 
    }}
    onPress={() => handleOptionClick('testPage')}
  >
    <Text style={{ color: '#fff', textAlign: 'center', fontSize: 16 }}>Test Sayfas</Text>
  </TouchableOpacity>
  <TouchableOpacity 
    style={{ 
      backgroundColor: '#FF7F50', 
      padding: 10, 
      borderRadius: 8, 
      marginBottom: 10 
    }}
    onPress={() => handleOptionClick('sendMessage')}
  >
    <Text style={{ color: '#fff', textAlign: 'center', fontSize: 16 }}>Mesaj Gnder</Text>
  </TouchableOpacity>
  <TouchableOpacity 
    style={{ 
      backgroundColor: '#ccc', 
      padding: 10, 
      borderRadius: 8 
    }}
    onPress={() => setModalIsOpen(false)}
  >
    <Text style={{ color: '#333', textAlign: 'center', fontSize: 16 }}>Kapat</Text>
  </TouchableOpacity>
</Modal>

      </View>
    );
  }

  if (currentPage === 'testPage') {
    return (
      <View style={styles.container}>
        <Text style={styles.title}>
          {selectedChatUser?.email}'un sayfas!
        </Text>
        <Button title="Geri Dn" onPress={() => setCurrentPage('home')} color="#FF7F50" style={styles.button} />
      </View>
    );
  }
  
  if (currentPage === 'chat') {
    return (
      <KeyboardAvoidingView
        style={styles.container}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
        keyboardVerticalOffset={Platform.OS === 'ios' ? 60 : 0}
      >
        <FlatList
          data={messages}
          keyExtractor={(item) => item.id}
          renderItem={({ item }) => {
            const isCurrentUser = item.user === user.email;
            return (
              <View
                style={[
                  styles.chatItem,
                  isCurrentUser ? styles.currentUserMessage : styles.otherUserMessage,
                ]}
              >
                <Text style={styles.chatText}>{item.text}</Text>
              </View>
            );
          }}
          contentContainerStyle={{paddingVertical: 10}} 
        />
        <View style={styles.inputContainer}>
          <TextInput
            ref={messageInputRef}
            value={message}
            onChangeText={setMessage}
            placeholder="Mesaj yazn"
            style={styles.messageBox}
            onSubmitEditing={handleSendMessageAndKeepFocus} // Enter tuuna baslnca mesaj gnderme ve odak kalma
            returnKeyType="send" // Klavyede 'Send' grnmesini salar
          />
          <TouchableOpacity style={styles.sendButton} onPress={handleSendMessageAndKeepFocus}>
            <Text style={styles.sendButtonText}>Gnder</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.backButton} onPress={() => setCurrentPage('home')}>
            <Text style={styles.backButtonText}>Geri Dn</Text>
          </TouchableOpacity>
        </View>
      </KeyboardAvoidingView>
    );
  }

  return null;
};

export default App;

